@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  html,
  body {
    min-height: 100%;
  }
  body {
    background-color: var(--background);
    font-family: var(--font-geist);
    color: var(--foreground);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: radial-gradient(
        1200px 500px at 50% -20%,
        rgba(148, 163, 184, 0.14),
        transparent 55%
      ),
      linear-gradient(180deg, #f8fafc 0%, #f5f7fa 58%, #f3f5f7 100%);
    position: relative;
    isolation: isolate;
  }
  body::before {
    content: "";
    position: fixed;
    inset: 0;
    pointer-events: none;
    z-index: -1;
    background-image: radial-gradient(
      rgba(148, 163, 184, 0.18) 0.6px,
      transparent 0.6px
    );
    background-size: 12px 12px;
    opacity: 0.35;
  }
  :where(a, button, input, textarea, select, [role="button"]):focus-visible {
    outline: 2px solid var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline: 2px solid color-mix(in srgb, var(--ring) 52%, white);
    }
    outline-offset: 2px;
  }
  ::selection {
    background: color-mix(in srgb, #1d4ed8 24%, white);
    @supports (color: color-mix(in lab, red, red)) {
      background: color-mix(in srgb, var(--color-brand) 24%, white);
