.premium-mesh {
  position: relative;
  isolation: isolate;
  overflow: hidden;
  background: radial-gradient(
      70% 70% at 20% 15%,
      rgba(148, 163, 184, 0.14),
      transparent 70%
    ),
    radial-gradient(
      75% 70% at 90% 20%,
      rgba(203, 213, 225, 0.14),
      transparent 75%
    ),
    linear-gradient(180deg, rgba(255, 255, 255, 0.95), rgba(246, 248, 250, 0.92));
