.premium-mesh::before,
.premium-mesh::after {
  content: "";
  pointer-events: none;
  position: absolute;
  z-index: -1;
  border-radius: 999px;
}
.premium-mesh::before {
  top: -20%;
  left: -12%;
  height: 32rem;
  width: 32rem;
  background: radial-gradient(
    circle at center,
    rgba(148, 163, 184, 0.22),
    rgba(148, 163, 184, 0.1) 38%,
