.premium-mesh::after {
  right: -14%;
  bottom: -28%;
  height: 30rem;
  width: 30rem;
  background: radial-gradient(
    circle at center,
    rgba(203, 213, 225, 0.22),
    rgba(203, 213, 225, 0.1) 36%,
    transparent 72%
  );
  animation: mesh-drift 18s ease-in-out infinite alternate-reverse;
