.premium-ambient::before {
  content: "";
  pointer-events: none;
  position: absolute;
  inset: -40% -20%;
  background: radial-gradient(
      28rem 16rem at 12% 8%,
      rgba(148, 163, 184, 0.14),
      transparent 62%
